}

.ispinner.ispinner--large {
    width: 35px;
    height: 35px;
}

.ispinner.ispinner--large .ispinner__blade {
    width: 8.5714285714%;
    height: 25.7142857143%;
    border-radius: 50%/16.67%;
}

@-webkit-keyframes ispinner__blade--fade {
    0% {
        opacity: 0.85;
    }
    50% {
        opacity: 0.25;
    }
    100% {
        opacity: 0.25;
    }
}

@keyframes ispinner__blade--fade {
    0% {
        opacity: 0.85;
    }
    50% {
